.contact {
  display: flex;
  flex-direction: column;
}
.contact h1 {
  font-size: 4.5rem;
  margin-bottom: 3rem;
}
.contact span {
  color: var(--main-color);
}
.contact form {
  max-width: 70rem;
  margin: 1rem auto;
  text-align: center;
  margin-bottom: 3rem;
}
.contact form .input-box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.contact form .input-box input,
.contact form textarea {
  width: 100%;
  padding: 1.5rem;
  font-size: 1.6rem;
  color: var(--text-color);
  background: var(--second-bg-color);
  border-radius: 0.8rem;
  margin: 0.7rem 0;
}

.contact form .input-box input {
  width: 49%;
}

.contact button {
  margin-top: 3rem;
}

@media (max-width: 768px) {
  .title {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
